.myBtn {
   display: flex;
   justify-content: center;
   align-items: center;
   width: 150px;
   background-color: #e63946;
   border: none;
   border-radius: 10px;
   color: white;
   padding: 15px 32px;
   text-decoration: none;
   display: inline-block;
   font-size: 16px;
   margin: 4px 2px;
   cursor: pointer;
}